@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.vertical-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: fixed;
  width: 200px;
  height: 100%;
  max-width: 400px;
  right: 0;
  transform-origin: right bottom;
}
.vertical-nav::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ebdada;
  transform: translateX(100%);
  transition: transform 0.4s;
}

.vertical-nav:hover::before {
  transform: translateX(0);
}

.vertical-nav .label {
  display: block;
  transform: translateX(100%);
  transition: transform 0.4s;
  font-size: 14px;
  margin: 3em 2em 1.8em 0em;
}

.vertical-nav:hover .label {
  transform: translateX(0);
}

.vertical-nav.open {
  transform: scale(1);
}


.vertical-nav ul {
  list-style: none;
}

.vertical-nav a {
  position: relative;
  padding: 3em 0 0;
  margin: 1em auto;
  text-decoration: none;
  color: rgb(83, 78, 78);
}

.vertical-nav a::before, .vertical-nav::after {
  content: '';
  position: absolute;
  left: 50%;
  transition: transform 0.4s 0s;
}

.vertical-nav a::before {
  top: -3.5em;
  height: 30px;
  width: 30px;
  margin: 1em 0em 1em 0em;
  border-radius: 50%;
  background: #d6c2c2;
  transform: translateX(-50%) scale(0.25);
}

.vertical-nav li {
  margin-bottom: 4em;
}


.vertical-nav:hover a::before,
.vertical-nav:hover a::after {
  transform: translateX(-100%) scale(1.5);
}

.content {
  height: 100vh;
  width: 100vw; 
  scroll-behavior: smooth;
}

.content #one, .content #two, .content #three {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.content #one {
  background-color: rgb(173, 39, 61);
}

.content #two {
  background-color: rgb(197, 71, 48);
}

.content #three {
  background-color: rgb(28, 34, 70);
}
h1 {
  font-size: 64px;
  color: #fff;
}

